.body_loaded {
  .social-block__toggler {
    &::before {
      transform: scale(1, 1);
    }
  }
}

.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      pointer-events: auto;

      .social-block__link {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  color: $c-dark;
  transition: color 0.2s;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    border: 2px solid $c-light;
    border-radius: 50%;
    background-color: $c-light;
    transition: transform 0.4s cubic-bezier(0.58, 0.27, 0.35, 1.53) 0.7s;
    transform: scale(0, 0);
    transform-origin: center center;
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: $c-purple;

    &::before {
      transition: transform 0.2s ease-out;
      transform: scale(1.125, 1.125);
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;

    &::before {
      border-color: $c-purple;
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      border-color: $c-purple;
      background-color: $c-purple;
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 100%;
    left: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  border: 2px solid transparent;
  border-radius: 50%;
  color: $c-light;
  opacity: 0;
  transition:
    opacity 0.3s,
    transform 0.3s,
    color 0.2s,
    background-color 0.2s,
    border-color 0.2s;
  transform: translateY(10px);

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    background-color: $c-dark;
    color: $c-light;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    background-color: $c-dark;
    color: $c-light;
  }

  &:hover {
    color: $c-purple;
  }

  &:focus {
    border-color: $c-purple;
    background-color: $c-light;
    color: $c-purple;
    outline: none;
  }

  &:active,
  &:active:focus {
    border-color: $c-light;
    background-color: $c-light;
    color: $c-purple;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    transition-delay:
      0.1s,
      0.1s,
      0s,
      0s,
      0s;

    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    transition-delay:
      0.2s,
      0.2s,
      0s,
      0s,
      0s;

    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
